import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Bouffe } from './Bouffe';
import { Router } from '@angular/router';

const optionRequete = {
  headers: new HttpHeaders({
    'Access-Control-Allow-Origin': '*',
    'mon-entete-personnalise': 'maValeur'
  })
};

@Injectable({
  providedIn: 'root'
})
export class BouffeService {

  deleteBouffe(idbouffe: number) {    
    return this.httpClient.delete(environment.apiUrl + '/bouffe/' + idbouffe, optionRequete);
  }
  
  getAllBouffes(): Observable<any> {
    return this.httpClient.get(environment.apiUrl + '/bouffe/all', optionRequete);
  }
  
  addBouffe(uneBouffe: import("@angular/forms").FormGroup): Observable<any> {
    let bouffe: string;
    let nombreBouffe: number;
    let repas: string;
    let typeNombreBouffe : string;

    bouffe = uneBouffe.value.bouffe;
    nombreBouffe = uneBouffe.value.nombreBouffe;
    typeNombreBouffe = uneBouffe.value.typeNombreBouffe;
    repas = uneBouffe.value.repas;

    console.warn(bouffe + " : " + nombreBouffe + " : " + typeNombreBouffe + " : " + repas);
    return this.httpClient.post(environment.apiUrl + '/bouffe', { bouffe: bouffe, nombreBouffe: nombreBouffe, typeNombreBouffe: typeNombreBouffe, repas: repas }, optionRequete)
  }

  constructor(private httpClient: HttpClient) { }
}
