import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Validators } from '@angular/forms';
import { BouffeService } from '../bouffe.service';
import { ThrowStmt } from '@angular/compiler';

@Component({
  selector: 'app-add-food',
  templateUrl: './add-food.component.pug',
  styleUrls: ['./add-food.component.css']
})
export class AddFoodComponent implements OnInit { 

  uneBouffe = this.fb.group({
    bouffe: ['', Validators.required],
    nombreBouffe: ['', Validators.required],
    typeNombreBouffe: ['', Validators.required],
    repas: ['Matin', Validators.required],
  });

  errors: string[];
  success: string[];

  constructor(private fb : FormBuilder,
              private bouffeService: BouffeService) { }

  ngOnInit(): void {
    this.errors = [];
    this.success = [];
  }

  onSubmit() {
    this.bouffeService.addBouffe(this.uneBouffe)
      .subscribe(result => {

        if (this.errors.length === 0) {
          this.success.push('votre repas a bien été crée');
          this.uneBouffe.reset();
        }
      });
  }
}
